import React, { Component } from 'react';

import Lead from './Lead';
import store from '../state';

class Stage extends Component {

	constructor() {
		super();

		this.state = {
			stages: [
				{id: '1', name: 'Cold Leads',
					leads: [
						{id: '10', name: 'John Smith'},
						{id: '11', name: 'Jessica Feathers'}
					]
				},
				{id: '2', name: 'Qualified Leads', leads: []},
				{id: '3', name: 'Prospects', leads: []},
				{id: '4', name: 'Clients', leads: []}
			]
		}
	}

	render() {
		var stages = this.state.stages.map(function(stage) {
			console.log(stage);
	       return (
			    <div className='dashboard__stage' key={stage.id}>
			      <div className='dashboard__head'>
			      {stage.name}
			      </div>
			      <Lead />
				</div>
			);

	    });


		return (<div className='dashboard'>{stages}</div>);
	}
}

store.subscribe(() => console.log('State ', store.getState()));
store.dispatch({
	type: 'STAGES_LOADED',
	stages: [{id: 1}]
})
module.exports = Stage;
